<template>
    <div class="add-message">
        <el-button @click="add()" type="primary" v-if="!isShow">添加</el-button>
        <el-table :data="tableData" border style="width: 100%">
            <el-table-column prop="name" label="二维码名称"></el-table-column>
            <el-table-column prop="content" label="关注后回复内容"></el-table-column>
            <el-table-column prop="gonghao" label="公号"></el-table-column>
            <el-table-column prop="qr_code_url" label="二维码">
                <template slot-scope="scope">
                    <img class="qr_img" :src="scope.row.qr_code_url" />
                </template>
            </el-table-column>
            <el-table-column label="操作">
                <template slot-scope="scope">
                    <el-button @click="update(scope.row)" type="primary" size="small">修改</el-button>
                    <el-button @click="deleteOne(scope.row)" type="danger" size="small">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
        <div class="wrapper" v-if="isShow">
            <h3 class="wrap-title">添加/修改二维码</h3>
            <el-form label-width="120px" size="mini">
                <el-form-item label="二维码名称" prop="name">
                    <el-input v-model="name" style="width:300px;"></el-input>
                </el-form-item>
                <el-form-item label="关注回复内容" prop="content">
                    <el-input type="textarea" rows="5" v-model="content" style="width:300px;"></el-input>
                </el-form-item>
                <el-form-item label="选择公号">
                    <el-radio-group v-model="code">
                        <el-radio style="margin: 10px;" v-for="(item, index) in gonghaoList" :key="index" :label="item.code">{{item.name}}</el-radio>
                    </el-radio-group>
                </el-form-item>
                <el-form-item size="large">
                    <el-button type="primary" @click="save" v-if="isUpdate">保存</el-button>
                    <el-button type="primary" @click="create" v-else>立即生成</el-button>
                </el-form-item>
            </el-form>
        </div>
    </div>
</template>

<script>
    import Axios from 'axios'
    export default {
        data() {
            return {
                name: '',
                content: '',
                gonghao: '',
                code: '',
                qr_code_url: '',
                gonghaoList: [],
                tableData: [],
                isUpdate: false,
                isShow: false
            }
        },
        mounted(){
            this.showList()
            this.getGonghaoList()
        },
        methods: {
            add() {
                this.isShow = true
            },
            create() {
                Axios({
                    url: '/qr_code/create',
                    method: 'post',
                    data: {
                        name: this.name,
                        content: this.content,
                        code: this.code
                    }
                }).then(res => {
                    if(res.data.data == '') {
                        this.$message.error('创建失败')
                    } else {
                        this.$message({
                            type: 'success',
                            message: '创建成功'
                        })
                        this.showList()
                        this.isShow = false
                    }
                })
            },
            getGonghaoList() {
                Axios({
                    url: '/qr_code/get_code',
                    method: 'get'
                }).then(res => {
                    this.gonghaoList = res.data.codes
                })
            },
            showList() {
                Axios({
                    url: '/qr_code/show',
                    method: 'get'
                }).then(res => {
                    var codes = res.data.codes
                    var data = res.data.data
                    for(var i=0,codesLength=codes.length;i<codesLength;i++) {
                        for(var j=0,dataLength=data.length;j<dataLength;j++){
                            if(codes[i].code == data[j].code) {
                                data[j].gonghao = codes[i].name
                            }
                        }
                    }
                    this.tableData = data
                })
            },
            update(row) {
                console.log(row)
                this.isUpdate = true
                this.name = row.name
                this.content = row.content
                this.code = row.code
                this.qr_code_url = row.qr_code_url
                this.isShow = true
            },
            deleteOne(row) {
                Axios({
                    url: '/qr_code/deleteOne',
                    method: 'post',
                    data: {
                        id: row._id
                    }
                }).then(res => {
                    this.$message({
                        type: 'success',
                        message: '删除成功'
                    })
                    this.showList()
                })
            },
            save() {
                Axios({
                    url: '/qr_code/update',
                    method: 'post',
                    data: {
                        name: this.name,
                        content: this.content,
                        code: this.code
                    }
                }).then(res => {
                    console.log(res.data.data)
                    if(res.data.data == '') {
                        this.$message.error('修改失败')
                    } else {
                        this.$message({
                            type: 'success',
                            message: '修改成功'
                        })
                        this.showList()
                        this.isShow = false
                    }
                })
            }
        }
    };
</script>

<style scoped>
    .wrapper{
        margin-top: 30px;
        border: 1px solid #eee;
    }
    
    .wrap-title{
        padding: 20px;
        text-align: center;
    }

    .qr_code_img{
        width: 200px;
        height: 200px;
        border: 1px solid #eee;
    }
</style>




